@import url("https://fonts.googleapis.com/css2?family=Fredoka+One&family=Montserrat&display=swap");
.Lyrics {
    min-height: 100vh;
    height: auto;
    text-align: center;
    font-family: "Montserrat", sans-serif;
	background-color: #ffffff;
}
.h1Style {
    font-family: "Fredoka One", cursive;
    color: #113a5d;
}
  
  
pre {
    font-size: 24px;
    color: rgba(0, 0, 0, 0.82);
}
  
hr {
    margin-top: 20px;
    width: 60%;
}
  
.inp {
    width: 200px;
    height: 40px;
    border: 2px solid #113a5d;
    margin: 0px 10px;
    padding-left: 6px;
    font-size: 22px;
}
  
.btn {
    width: 120px;
    height: 50px;
	background-color: #113a5d;
	color: white;
	font-size: 22px;
}
.btn:hover{
    width: 120px;
    height: 50px;
	background-color: #ffffff;
	color: #113a5d;
	font-size: 22px;
}